import Table from "react-bootstrap/Table";
import Pokemon from "./Pokemon";
import './DisplayPokemonTable.css';

function DisplayPokemonTable({pokemons, type}) {
    const pokemonArray = pokemons.filter(pokemon => pokemon.types.toString().toLowerCase().indexOf(type) >= 0 );

    return(
        <div>
            <h2><strong>{type}</strong></h2>
            <Table striped bordered hover>
                <thead>
                <tr>
                    <th>ID</th>
                    <th>Name</th>
                    <th>Image</th>
                    <th>Type(s)</th>
                </tr>
                </thead>
                <tbody>
                {
                    pokemonArray.map( (pokemon) => {
                        return <Pokemon name={pokemon.name} image={pokemon.img} types={pokemon.types} id={pokemon.ntnlnum} key={pokemon.ntnlnum} />
                    })
                }
                </tbody>
            </Table>
            <p>{pokemonArray.length} characters</p>
        </div>
    )
}
export default DisplayPokemonTable;
