import './Pokemon.css';
import { Link } from 'react-router-dom';

function Pokemon(props) {
    let url = '/' + props.name.toLowerCase();
    return (
        <tr>
            <td>
                {props.id}
            </td>
            <td>
                <Link to={url}>{props.name}</Link>
            </td>
            <td>
                <img src={props.image} alt=""/>
            </td>
            <td>
                {
                    props.types.map((item, index) => {
                        if(index === 0) {
                            return item
                        }
                        else {
                            return ', ' + item
                        }
                    })
                }
            </td>
        </tr>
    )
}

export default Pokemon;
