import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Route, Link, Switch } from 'react-router-dom';
import DisplayPokemonTable from "./components/DisplayPokemonTable";
import PokemonDetails from "./components/PokemonDetails";
import {connect} from "react-redux";

const App = ({pokemons, types}) => {
    return (
        <div className="App">
            <header>
                <h1>Pokedex Activity</h1>
            </header>
            <main>
                <nav className="Menu">
                    <Link to='/home'>Home </Link>
                    {
                        types.map((item, index) => {
                            let url = '/types/' + item.toLowerCase();
                            return(<Link to={url} key={index}>| {item} </Link>)
                        })
                    }
                </nav>

                <Switch>
                    <Route exact path="/home">
                        <div>
                            <h2>Home</h2>
                            <div className="container-home">
                                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aperiam, debitis, expedita? A dicta eligendi iure, laudantium maiores nobis provident ratione repellat temporibus voluptatem. Accusantium animi iste nostrum nulla repellat vero.</p>
                            </div>
                        </div>
                    </Route>

                    {
                        types.map((item, index) => {
                            return(
                                <Route path={"/types/"+item.toLowerCase()} key={index}>
                                    <DisplayPokemonTable pokemons={pokemons} type={item.toLowerCase()} />
                                </Route>
                            )
                        })
                    }

                    {
                        pokemons.map((pokemon, index) => {
                            return(
                                <Route exact path={"/"+pokemon.name.toLowerCase()} key={index}>
                                    <PokemonDetails pokemon={pokemon} />
                                </Route>
                            )
                        })
                    }
                </Switch>
            </main>
        </div>
    );
}

const mapStateToProps = state => {
    return {
        pokemons: state.pokemons,
        types: state.types,
    }
}

export default connect(mapStateToProps)(App);
